;IACvB,wBAAwB;IACxB,gBAAgB;AACpB;AAEA;IACI,yBAAyB;AAC7B;AAEA;IACI,2BAA2B;AAC/B;AAEA;IACI,mCAAmC;IACnC,+BAA+B;IAC/B,2BAA2B;AAC/B;AAEA;IACI,mBAAmB;AACvB;AAEA;IACI,kBAAkB;AACtB;AAEA;IACI,aAAa;AACjB;AAEA;IACI,mBAAmB;AACvB;AAEA;IACI,mBAAmB;AACvB;AAEA;IACI,oCAAoC;IACpC,gCAAgC;AACpC;AAEA;IACI,2BAA2B;AAC/B;AAEA;IACI,aAAa;AACjB;AAEA;IACI,yBAAyB;IACzB,WAAW;IACX,WAAW;AACf;AAEA;IACI,2BAA2B;AAC/B;AACA;EACE,eAAe;EACf,iBAAiB;EACjB,cAAc;EACd,aAAa;EACb,mBAAmB;EACnB,iBAAiB;AACnB;AACA;IACI,oCAAoC;IACpC,eAAe;AACnB;AAEA;IACI,kBAAkB;AACtB;AAEA;IACI,gBAAgB;IAChB,iBAAiB;IACjB,mBAAmB;IACnB,YAAY;AAChB;AAEA;IACI,eAAe;IACf,iBAAiB;IACjB,qBAAqB;IACrB,uBAAuB;IACvB,kBAAkB;IAClB,eAAe;AACnB;AAEA;IACI,UAAU;IACV,kBAAkB;IAClB,kBAAkB;IAClB,6BAA6B;IAC7B,WAAW;IACX,YAAY;IACZ,gBAAgB;IAChB,aAAa;IACb,UAAU;IACV,mBAAmB;IACnB,8BAA8B;IAC9B,yDAAyD;IACzD,wBAAwB;IACxB,+CAA+C;IAC/C,iCAAiC;IACjC,UAAU;IACV,kBAAkB;IAClB,UAAU;AACd;AAEA;IACI,gBAAgB;AACpB;AAEA;IACI,gBAAgB;AACpB;AAEA;IACI,kBAAkB;IAClB,WAAW;IACX,WAAW;IACX,uBAAuB;IACvB,SAAS;IACT,mBAAmB;IACnB,yBAAyB;IACzB,8BAA8B;IAC9B,6DAA6D;IAC7D,yBAAyB;IACzB,8BAA8B;IAC9B,6BAA6B;IAC7B,4BAA4B;IAC5B,wBAAwB;IACxB,iBAAiB;IACjB,8BAA8B;AAClC;AAEA;IACI,kBAAkB;IAClB,gBAAgB;IAChB,eAAe;IACf,YAAY;IACZ,mBAAmB;IACnB,UAAU;IACV,gBAAgB;IAChB,gBAAgB;IAChB,iBAAiB;AACrB;AAEA;IACI,kBAAkB;IAClB,eAAe;IACf,sBAAsB;IACtB,cAAc;IACd,UAAU;AACd;AAEA;IACI,aAAa;IACb,eAAe;IACf,iBAAiB;AACrB;AAEA;IACI,YAAY;AAChB;;AAEA,YAAY;AACZ;IACI,2BAA2B;IAC3B,6BAA6B;IAC7B,yBAAyB;IACzB,UAAU;IACV,mCAAmC;IACnC,kCAAkC;IAClC,oBAAoB;IACpB,WAAW;IACX,YAAY;IACZ,wDAA+C;IAC/C,2DAAkD;AACtD;AAEA;AACI;QACI,4BAA4B;AAChC;AAEA;QACI,8BAA8B;AAClC;AACJ;AAEA;AACI;QACI,+BAA+B;AACnC;AAEA;QACI,iCAAiC;AACrC;AACJ;AAEA;AACI;QACI,4BAA4B;AAChC;AAEA;QACI,+BAA+B;AACnC;AACJ;AAEA;AACI;QACI,+BAA+B;AACnC;AAEA;QACI,kCAAkC;AACtC;AACJ;AAEA;IACI,WAAW;IACX,YAAY;IACZ,yBAAyB;IACzB,qBAAqB;IACrB,uBAAuB;IACvB,mCAAmC;IACnC,kCAAkC;IAClC,iBAAiB;AACrB","sourcesContent":["<script setup lang=\"ts\">\nimport { computed } from \"@vue/reactivity\";\nimport { defineAsyncComponent, onMounted } from 'vue';\nimport { container } from \"../../../../domain/core/diContainer\";\nimport { IDialog } from \"../../../../domain/presentation/IDialog\";\nimport { INotification } from \"../../../../domain/presentation/INotification\";\nimport { IStudio } from \"../../../../domain/useCase/IStudio\";\nimport { IUseCaseExecutor } from \"../../../../domain/useCase/IUseCaseExecutor\";\nimport { IViewModel } from \"../../../../domain/viewModel/IViewModel\";\nimport \"bootstrap/js/dist/dropdown\";\nimport Tabs from \"../../../vue3/components/ui/tabs.vue\";\nimport { ITab } from \"../../../vue3/components/ui/ITabs\";\nimport { ref } from \"vue\";\nimport { IRouter } from \"../../../../domain/presentation/IRouter\";\nimport { FileSystem } from \"../../../../domain/infrastructure/fileSystem/fileSystem\";\nimport RadioButtonComp from \"../radioButtonComp.vue\";\nimport { ILivePreviewUseCase } from \"../../../../domain/useCase/ILivePreviewUseCase\";\nimport { IExportAppUseCase } from \"../../../../domain/useCase/IExportAppUseCase\";\nimport { ICustomLivePreview} from '../../../../../../common/qCloudTemp/livePreview';\nimport { IPage } from \"../../../../domain/presentation/IPage\";\nimport { NetworkSubSystem } from \"../../../../domain/infrastructure/fileSystem/networkSubSystem\";\nimport CheckboxComp from \"../checkboxComp.vue\";\nimport { IQJsonAdditionals } from \"../../../../../../common/qCloudTemp/quickCloud\";\nimport { IAuthenticationUseCase } from \"../../../../domain/useCase/IAuthenticationUseCase\";\nimport { IViewModelManager } from \"../../../../domain/viewModel/IViewModelManager\";\nimport { IPlatformSelection } from \"../../../../../../common/qCloudTemp/exporter\";\nimport { IQRCode } from \"../../../../domain/infrastructure/IQRCode\";\nimport { IApplicationSettingsUseCase } from \"../../../../domain/useCase/IApplicationSettingsUseCase\";\n\nconst authentication = container.resolve<IAuthenticationUseCase>(IAuthenticationUseCase);\nconst viewModel = container.resolve<IViewModel>(IViewModel);\nconst viewModelManager = container.resolve<IViewModelManager>(IViewModelManager);\nconst appSettingsUseCase = container.resolve<IApplicationSettingsUseCase>(IApplicationSettingsUseCase);\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\nconst studio = container.resolve<IStudio>(IStudio);\nconst notification = container.resolve<INotification>(INotification);\nconst dialog = container.resolve<IDialog>(IDialog);\nconst page = container.resolve<IPage>(IPage);\nconst fileSystem = container.resolve<FileSystem>(FileSystem);\nconst user = viewModel.user!;\n\nconst livePreviwUseCase = container.resolve<ILivePreviewUseCase>(ILivePreviewUseCase);\nconst exportAppUseCase = container.resolve<IExportAppUseCase>(IExportAppUseCase);\nconst router = container.resolve<IRouter>(IRouter);\nconst routerParams = router.getRouteParams<{ appID: string }>();\nconst hoverValue = ref(false);\nconst previewType = ref<string>(\"default\");\nconst exportAppType = ref<string>(\"create\");\nconst showCustomPreview = ref(false);\nconst previewDropdownOpen = ref(false);\nconst showQRCode = ref(false);\nconst showLoading = ref(true);\nconst customPreviewLastUpdated = ref();\nconst publishToWeb = ref(false);\nconst publishToMobile = ref(false);\nconst currentApp = computed(() => viewModel.apps.find((app) => app.ID == viewModel.studio.appId));\nconst studioAppLink = ref(\"\");\n\nconst hasModel = computed(() => {\n    return viewModelManager.traverseChildren(viewModel.studio.items, (item) => {\n        return item.objectType == \"model\";\n    });\n});\n\nconst hasQJson = computed(() => {\n    return viewModelManager.traverseChildren(viewModel.studio.items, (item) => {\n        return item.objectType == \"model\" && item.modelType == \"qjson\";\n    });\n});\n\nconst hasRootPage = computed(() => {\n    // const appSettingsModel = appSettingsUseCase.getStartScreen();\n    // return !!appSettingsModel;\n\n    return viewModelManager.traverseChildren(viewModel.studio.items, (item) => {\n        return !!(item.objectType == \"model\" && item.modelType == \"qjson\" && (item.additionals as IQJsonAdditionals).isRootPage);\n    });\n});\n\nconst isDeployAllowed = computed(() => viewModel.user?.selectedOrganization?.cloud);\n\nconst operationState = {\n    publishing: ref(false),\n    downloading: ref(false),\n}\n\nonMounted(() => {\n    document.getElementById(\"ulDropdown\")?.addEventListener(\"click\", (event: any) => {\n        if (event.target.id == \"custom-preview\") return;\n        event.stopPropagation();\n    })\n    document.getElementById(\"ulDropdownExport\")?.addEventListener(\"click\", (event: any) => {\n        if (event.target.id == \"see-progress-details\") return;\n        event.stopPropagation();\n    })\n    document.addEventListener(\"click\", () => {\n        const ariaExpanded = document.getElementById(\"dropdownLivePreviewButton\")?.getAttribute(\"aria-expanded\");\n        previewDropdownOpen.value = ariaExpanded == \"true\";\n    })\n})\n\nfunction getUserName() {\n  const user = viewModel.user!;\n  if (user.name && user.lastName) { return `${user.name} ${user.lastName}`; }\n  return user.email;\n}\n\nfunction goToProfile() {\n  router.push(\"/completeProfile\");\n}\n\nconst tablist = computed(() => {\n    return (\n        viewModel.studio?.openItems?.map((item) => {\n            const retVal: ITab = {\n                name: item.name,\n                type: item.modelType,\n                id: item.ID,\n                selected: viewModel.studio.currentItem?.ID == item.ID,\n                modified: item.modified?.model,\n                synced: item.synced,\n                cb: {\n                    close: executor.wrap(async () => studio.closeItem(item), { loading: false }),\n                    select: executor.wrap(async () => studio.openItem(item), { loading: true }),\n                },\n            };\n            return ref(retVal).value;\n        }) || []\n    );\n});\n\n\nasync function goMainPage() {\n    if (viewModel.studio.openItems && viewModel.studio.openItems.some(item => item.checkedOut && item.modified?.model)) {\n        const result = await notification.showConfirm({\n            type: \"warning\",\n            title: `You are leaving the page.`,\n            text: \"Changes you made may not be saved.\",\n            showCloseIcon: true,\n            approveText: \"Confirm\",\n            cancelText: \"Cancel\",\n        });\n        if (result.result == \"cancel\") {\n            return;\n        }\n    }\n    router.push(`/main/applicationList/${viewModel.user!.selectedOrganization!.id}`);\n}\n\nfunction logout() {\n    executor.execute(async () => { await authentication.signOut(); }, { loading: true });\n}\n\nfunction openCustomPreviewSettingsScreen() {\n    dialog.showDialog(\n        defineAsyncComponent(() => import(`../dialogs/preview/customPreviewSettings.vue`)),\n        { closable: true, title: \"Custom Preview Settings\", width: \"800px\", height: \"705px\" }\n    );\n    document.getElementById(\"dropdownLivePreviewButton\")!.setAttribute(\"aria-expanded\", \"false\")\n}\n\nfunction hoverLogo() {\n    hoverValue.value = true;\n}\nfunction unHover() {\n    hoverValue.value = false;\n}\n\nfunction setExportType(value: string){\n    exportAppType.value = value;\n}\n\nasync function checkPreviews() {\n    showLoading.value = true;\n    const ariaExpanded = document.getElementById(\"dropdownLivePreviewButton\")!.getAttribute(\"aria-expanded\");\n    if (ariaExpanded == \"false\") return;\n    let livePreviewList = await livePreviwUseCase.listLivePreviewInfo(viewModel.studio.appId);\n    showLoading.value = false;\n\n    if (livePreviewList && livePreviewList.length > 0) {\n        const customPreview = livePreviewList.find(lp => lp.type == \"custom\") as ICustomLivePreview;\n        const selectedPreview = livePreviewList.find(lp => lp.selected);\n        previewType.value = selectedPreview!.type;\n        if (!showQRCode.value) {\n            showQRCode.value = true;\n            const canvas = document.getElementById(\"qrcode\");\n            await livePreviwUseCase.createLivePreviewQRCode(canvas!, selectedPreview!.appID);\n        }\n        if (customPreview) {\n            const date = new Date(customPreview.creationInfo.date)\n            customPreviewLastUpdated.value = date.getDate() + \".\" + (date.getMonth() + 1) + \".\" + date.getFullYear() + \" \" + (date.getHours() < 10 ? \"0\" : \"\") + date.getHours() + \":\" + (date.getMinutes() < 10 ? \"0\" : \"\") + date.getMinutes();\n            showCustomPreview.value = true;\n        }\n    } else {\n        showCustomPreview.value = false;\n        previewType.value = \"default\";\n    }\n}\n\nasync function openLivePreviewTab() {\n    const appID = viewModel.studio.appId;\n    const appName = viewModel.studio.appName;\n\n    await executor.execute(async () => {\n        let lpItem = await livePreviwUseCase.getLivePreviewInfo(appID, { selected: true });\n        if (!lpItem) {\n            await livePreviwUseCase.createLivePreview({ appID, type: \"default\", selected: true });\n        }\n        window.open(`../../livepreview/${appID}/${appName}`, \"_blank\")\n    }, { loading: true });\n}\n\nfunction openFeedbackDialog () {\n  dialog.showDialog(\n        defineAsyncComponent(() => import(`../../components/dialogs/giveUsFeedbackDialog.vue`)),\n        { closable: true, title: \"Give Us Feedback\", width: \"540px\", maxHeight: \"706px\" },\n    );\n}\nasync function triggerAutoSave() {\n    fileSystem.SubSystem =new NetworkSubSystem();\n    await fileSystem.Connect();\n    const applicationID = routerParams.appID;\n    exportAppUseCase.syncFiles(applicationID, fileSystem);\n\n\n}\n\nfunction handleStudioOrCustom(selectedType: string) {\n    if (showCustomPreview.value) {\n        executor.execute(\n            async () => {\n                await livePreviwUseCase.updateSelectedLivePreview(viewModel.studio.appId);\n                previewType.value = selectedType;\n            },\n            { loading: true }\n        )\n    }\n}\n\nfunction publishApplication() {\n    operationState.publishing.value = !operationState.publishing.value;\n    const platformSelection: IPlatformSelection = { web: publishToWeb.value, mobile: publishToMobile.value };\n    const title = platformSelection.web ? platformSelection.mobile ? \"Web and Mobile\" : \"Web\" : platformSelection.mobile ? \"Mobile\" : \"\"; \n    dialog.showDialog(\n        defineAsyncComponent(() => import(`../dialogs/publish/publishDialog.vue`)),\n        { closable: true, title: \"Publish Modules to \" + title, width: \"800px\", height: platformSelection.web ? \"622px\" : \"555px\" },\n        { screenType: \"Deploy\", platformSelection }\n    );\n}\n\nfunction exportAppClickEvent(){\n    if(exportAppType.value == \"create\")\n        downloadApplication();\n    else if(exportAppType.value == \"sync\")\n        triggerAutoSave();\n}\n\nfunction copyExportCommand(){\n    navigator.clipboard.writeText(\"symphony-cli studio-sync\");\n}\n\nfunction downloadApplication() {\n    operationState.downloading.value = !operationState.downloading.value;\n\n    dialog.showDialog(\n        defineAsyncComponent(() => import(`../dialogs/publish/publishDialog.vue`)),\n        { closable: true, title: \"Chose Modules to Download\", width: \"800px\", height: \"531px\" },\n        { screenType: \"Export\" }\n    );\n}\n\nfunction seeProgressDetails() {\n    page.openPage(\n        defineAsyncComponent(() => import('../dialogs/publish/publishHistory.vue')),\n        {name : \"Progress Details\"}\n    );\n}\nfunction checkPublishDetails() {\n    viewModel.studio.dnsEndpoint = currentApp.value?.cloud?.azure?.dnsEndpoint;\n    viewModel.studio.webPublishDate = currentApp.value?.cloud?.azure?.web?.publishDate.toLocaleString();\n    viewModel.studio.webPublishedBy = currentApp.value?.cloud?.azure?.web?.publishedBy.split(\"@\")[0];\n    viewModel.studio.mobilePublishDate = currentApp.value?.cloud?.azure?.mobile?.publishDate.toLocaleString();\n    viewModel.studio.mobilePublishedBy = currentApp.value?.cloud?.azure?.mobile?.publishedBy.split(\"@\")[0];\n    viewModel.studio.publishId = currentApp.value?.publishmentId;\n    mobilePublishedQRCode();\n}\nfunction mobilePublishedQRCode() {\n    const QRCode = container.resolve<IQRCode>(IQRCode);\n    const canvas = document.getElementById(\"studioapps-qrcode\") as HTMLElement;\n    studioAppLink.value = \"https://studio.onplateau.com/studioapps/\" + viewModel.studio.publishId;\n    QRCode.drawToCanvas(canvas, studioAppLink.value);\n\n}\nfunction calculateDate(createDate: string) {\n    const date = new Date(createDate);\n    if(date) {\n        const minutes = parseInt(String(((new Date()).getTime() - date.getTime()) / (60 * 1000)), 10)\n        const hours = parseInt(String(minutes / 60), 10);\n        const days = parseInt(String(hours / 24), 10);\n        const weeks = parseInt(String(days / 7), 10);\n        const months = parseInt(String(weeks / 4), 10);\n        const years = parseInt(String(months / 12), 10);\n        if(years > 0) {\n            return years.toFixed() + ` year${years < 2 ? '' : 's'} ago`;\n        } else if(months > 0) {\n            return months.toFixed() + ` month${months < 2 ? '' : 's'} ago`;\n        } else if(weeks > 0) {\n            return weeks.toFixed() + ` week${weeks < 2 ? '' : 's'} ago`;\n        } else if(days > 0) {\n            return days.toFixed() + ` day${days < 2 ? '' : 's'} ago`;\n        } else if(hours > 0) {\n            return hours.toFixed() + ` hour${hours < 2 ? '' : 's'} ago`;\n        } else if(minutes > 0) {\n            return minutes.toFixed() + ` minute${minutes < 2 ? '' : 's'} ago`;\n        }\n    }\n    return \"few seconds ago\";\n}\nfunction copyToClipboard(element: string, isFromInnerHtml: boolean) {\n    if(isFromInnerHtml) {\n        let text = document.getElementById(element)?.innerHTML;\n        navigator.clipboard.writeText(text!);\n    }\n    else {\n        navigator.clipboard.writeText(element);\n    }\n\n}\n\nfunction redirectToStore(store: string) {\n    switch(store) {\n        case \"apple\": window.open(\"https://apps.apple.com/tr/app/studioapps/id6446583055\", \"_blank\"); break;\n        // case \"google-play\": window.open(\"\", \"_blank\"); break;\n    }\n}\nfunction changeAppLogo() {\n    dialog.showDialog(\n        defineAsyncComponent(() => import(`../dialogs/appLogoDialog.vue`)),\n        { closable: true, title: \"Select App Image\", width: \"530px\" }, {app: currentApp.value}\n    );\n}\nfunction gotoBackOffice() {\n//   router.push(\"/backoffice/dashboard\");\n  goToUrl('/backoffice/dashboard');\n}\nfunction goToUrl(url: string) {\n  window.open(url, \"_blank\")\n}\n</script>\n<template>\n    <div class=\"top-menu\">\n        <div class=\"row top-container\">\n            <div class=\"col-lg-1 home-section\" style=\"display: flex\">\n                <div class=\"corner-container\">\n                    <div class=\"logo-container\" @click=\"goMainPage\" @mouseover=\"hoverLogo()\" @mouseleave=\"unHover()\"\n                        :style=\"{ 'background-color': hoverValue ? '#151233' : '', padding: '15px' }\">\n                        <img v-show=\"!hoverValue\" src=\"../../../../images/ide5-logo.svg\" alt=\"ide5-logo\" width=\"24\"\n                            height=\"24\" />\n                        <img v-show=\"hoverValue\" src=\"../../../../images/plateau_hover.svg\" alt=\"ide5-logo\" />\n                    </div>\n                </div>\n                <div class=\"vl\"></div>\n            </div>\n\n            <div class=\"col-lg-8 middle-resize\" style=\"display: flex; align-items:center\">\n                <div class=\"middle-container\">\n                    <div class=\"title-container\">\n                        <div class=\"app-logo-div\" @click=\"changeAppLogo()\">\n                            <img v-if=\"currentApp?.logoInfo && currentApp?.synced\" :src=\"`${currentApp.logoInfo.hostUrl}${currentApp.logoInfo.path}?c=${currentApp.logoInfo.updateDate}`\" alt=\"app-logo\" class=\"app-logo\" />\n                            <img v-if=\"!currentApp?.logoInfo\" src=\"../../../../images/ide5-logo.svg\" alt=\"ide-logo\" class=\"title-logo\" />\n                        </div>\n                       \n                        <span class=\"title\">{{ currentApp?.name }}</span>\n                    </div>\n                    <div class=\"tabs\">\n                        <Tabs :tabs=\"tablist\" height=\"30px\" ref=\"tabComp\"></Tabs>\n                    </div>\n                </div>\n            </div>\n            <div class=\"col-lg-3 last-resize\">\n                <div class=\"end-container position-absolute top-0 end-0\">\n\n                    <!-- Preview -->\n                    <div style=\"display: flex; margin-right: 12px; flex-direction: column; justify-content: center;\" :class=\"{'button-disabled': !hasQJson || !hasRootPage}\">\n                        <div class=\"preview-button-group\">\n                            <span style=\"margin-top: 7px;\">\n                                <div  type=\"button\" @click=\"openLivePreviewTab()\" class=\"preview-button\">\n                                    <span class=\"preview-play-icon\"><i class=\"mdi mdi-play mdi-20px\"> </i></span>\n                                    <span class=\"preview-text\">Preview</span>\n                                </div>\n                            </span>\n                            <span style=\"margin-top: 7px;\">\n                                <div type=\"button\" @click=\"checkPreviews\" class=\"preview-dropdown-button\"\n                                    id=\"dropdownLivePreviewButton\" data-bs-toggle=\"dropdown\" aria-expanded=\"false\">\n                                     <!-- <span class=\"preview-dropdown-icon\" :style=\"previewDropdownOpen ? 'transform: rotate(180deg); margin-top: 10px;' : ''\">󰅀</span> -->\n                                    <i class=\"mdi mdi-dots-vertical\" style=\"font-size: 20px\"></i>\n                                </div>\n                                <ul class=\"dropdown-menu menu-preview\" id=\"ulDropdown\"\n                                    :style=\"'box-shadow: 0px 2px 4px #00000029; border: 1px solid #D5D5D5;' + (showLoading ? 'height: 178px;' : showCustomPreview ? 'height: 523px' : showQRCode ? 'height: 485px;' : '')\"\n                                    aria-labelledby=\"dropdownLivePreviewButton\">\n                                    <div v-show=\"!showLoading\">\n                                        <li>\n                                            <div :style=\"{ margin: '25px 0px 0px 101px', width: '72.44px', height: '57.96px' }\">\n                                                <img src=\"../../../../images/projector.svg\" />\n                                            </div>\n                                        </li>\n                                        <li>\n                                            <span class=\"preview-dropdown-text\">Choose how would you like to preview your\n                                                application</span>\n                                        </li>\n                                        <hr class=\"preview-first-line\" />\n                                        <li>\n                                            <RadioButtonComp class=\"radio-button\" style=\"left: 20px\"\n                                                :active=\"previewType == 'default'\" :type=\"'preview'\" @click=\"handleStudioOrCustom('default')\" />\n                                            <span class=\"preview-menu-studio-text\">Studio Preview</span>\n                                            <br />\n                                            <span class=\"preview-menu-studio-description\">Qui Preview</span>\n                                        </li>\n                                        <hr class=\"preview-second-line\" />\n                                        <li v-if=\"showCustomPreview\">\n                                            <RadioButtonComp class=\"radio-button\" style=\"left: 20px\"\n                                                :active=\"previewType == 'custom'\" :type=\"'preview'\" @click=\"handleStudioOrCustom('custom')\" />\n                                            <span class=\"preview-menu-custom-text\">Custom Preview</span>\n                                            <br />\n                                            <span class=\"preview-menu-custom-description\">Last update {{ customPreviewLastUpdated }}</span>\n                                            <span id=\"custom-preview\" class=\"edit-custom-preview\"\n                                                @click=\"openCustomPreviewSettingsScreen()\">\n                                                <i class=\"mdi mdi-pencil\" style=\"font-size: 16px;\" /> Edit Custom Preview\n                                            </span>\n                                        </li>\n                                        <li v-if=\"!showCustomPreview\">\n                                            <div class=\"preview-menu-items\" @click=\"openCustomPreviewSettingsScreen()\">\n                                                <span id=\"custom-preview\" class=\"add-custom-preview\">\n                                                    <i class=\"mdi mdi-plus\" /> Add Custom Preview</span>\n                                            </div>\n                                        </li>\n                                        <hr class=\"preview-third-line\" v-show=\"showQRCode\"\n                                            :style=\"showCustomPreview ? 'margin-top: 69px;' : ''\" />\n                                        <li v-show=\"showQRCode || showCustomPreview\">\n                                            <span class=\"qrcode-text\">\n                                                You can also view your application on mobile by scanning QR code.\n                                            </span>\n                                            <canvas id=\"qrcode\" class=\"qrcode\" width=\"104\" height=\"104\"/>\n                                        </li>\n                                    </div>\n                                    <div v-show=\"showLoading\">\n                                        <div class=\"loading\"></div>\n                                        <div class=\"loading-text\">Loading</div>\n                                    </div>\n                                </ul>\n                            </span>\n                        </div>\n                        <span class=\"button-tooltip\" style=\"left: -70px; width: 280px\">\n                            To preview your app, you need to set up a start screen created outside of a module <p style=\"margin-bottom: 0\">Click <i style=\"background: #50505A\" class=\"mdi mdi-dots-horizontal\"></i> on UI Screen item that created outside of a module and use \"Set as Start screen\" option. </p> \n                        </span>\n                    </div>\n                    <!-- Export -->\n                    <div style=\"display: flex; flex-direction: column; justify-content: center;\">\n                        <div class=\"export-button-group\">\n                            <span style=\"margin-top: 7px;\">\n                                <div  type=\"button\" @click=\"exportAppClickEvent()\" class=\"export-button\">\n                                    <span style=\"font-style:normal !important\" class=\"export-icon\"><i class=\"mdi mdi-export mdi-20px\"> </i></span>\n                                    <span class=\"export-text\">Export</span>\n                                </div>\n                            </span>\n                            <span style=\"margin-top: 7px;\">\n                                <div type=\"button\" class=\"preview-dropdown-button\"\n                                    id=\"dropdownExportButton\" data-bs-toggle=\"dropdown\" aria-expanded=\"false\">\n                                    <i class=\"mdi mdi-dots-vertical\" style=\"font-size: 20px\"></i>\n                                </div>\n                                <ul class=\"dropdown-menu menu-export\" id=\"ulDropdownExport\"\n                                    :style=\"'box-shadow: 0px 2px 4px #00000029; border: 1px solid #D5D5D5;' +  (exportAppType == 'sync' ? 'height: 509px !important; ' : '')\"\n                                    aria-labelledby=\"dropdownExportButton\">\n                                    <div>\n                                        <li class=\"export-li-first\">\n                                            <div class=\"export-image\" >\n                                                <img src=\"../../../../images/export-box.svg\" />\n                                            </div>\n                                            <span class=\"export-dropdown-text\">Choose how would you like to export your application</span>\n                                        </li>\n                                        <li class=\"export-li-second\">\n                                            <RadioButtonComp class=\"export-radio-button\"\n                                                :active=\"exportAppType == 'create'\" :type=\"'export'\" @click=\"setExportType('create')\" />\n                                            <span class=\"export-radio-button-header-text\">Create an App Package</span>\n                                            <br />\n                                            <span style=\"top:200px;\" class=\"export-radio-button-description-text\">Download the zip file of your project to your device</span>\n                                        </li>\n                                        <li class=\"export-li-third\" :style=\"(exportAppType == 'sync' ? 'height: 205px !important; ' : '')\">\n                                            <RadioButtonComp class=\"export-radio-button\"\n                                                :active=\"exportAppType == 'sync'\" :type=\"'export'\" @click=\"setExportType('sync')\" />\n                                            <span class=\"export-radio-button-header-text\">Sync Files to Your Device</span>\n                                            <span v-if=\"exportAppType=='create'\" style=\"top:286px;\" class=\"export-radio-button-description-text\">Preliminary steps required.</span>\n                                            <span @click=\"copyExportCommand()\" v-if=\"exportAppType=='sync'\" style=\"top:286px;\" class=\"export-radio-button-sync-description-text\">\n                                                To perform this action, open a Command Prompt in your local app folder and run command below.\n                                                <p style=\"font-weight: 500; margin-top: 8px\">\n                                                    <span id=\"sym-command\">symphony-cli studio-sync</span>\n                                                    <i @click=\"copyToClipboard('sym-command', true)\" class=\"mdi mdi-content-copy copy-command\">\n                                                        <span class=\"button-tooltip\" style=\"left: 120px; top: 25px; width: 106px; font-style: normal;\">Copy to clipboard</span>\n                                                    </i>\n                                                </p>\n                                            </span>\n                                            <div v-if=\"exportAppType=='sync'\" style=\"position:absolute; width: 258px; height:52px; left: 20px; top:366px; margin-top: 20px;\">\n                                                <div style=\"position: absolute; width: 250px; height:52px; left:0px; top: 0px; background: #F5F5FA; border-radius: 5px;\">\n                                                    <span style=\"font-style:normal !important\" class=\"information-icon\"><i class=\"mdi mdi-information mdi-20px\"> </i></span>\n                                                    <span class=\"export-sync-documantation-text\">\n                                                        If this is your first time exporting, you can contact with Plateau Studio team.\n                                                    </span>\n                                                    <!-- <a class=\"documentationlink\" :href=\"documentationLink\" style=\"text-decoration:none\" target=\"_blank\">See documentation\n                                                        <i class=\"documantation-link-icon mdi mdi-open-in-new\"></i>\n                                                    </a> -->\n                                                </div>\n                                            </div>\n                                        </li>\n                                        <li class=\"export-li-fourth\">\n                                            <div class=\"export-see-progress-details-div\" @click=\"seeProgressDetails()\">\n                                                <span id=\"see-progress-details\" class=\"see-progress-details-text\">\n                                                     See Progress Details</span>\n                                            </div>\n                                        </li>\n                                    </div>\n                                </ul>\n                                \n                            </span>\n                        </div>\n                        <span class=\"button-tooltip\" style=\"left: -70px; width: 280px\">\n                            To preview your app, you need to set up a start screen created outside of a module <p style=\"margin-bottom: 0\">Click <i style=\"background: #50505A\" class=\"mdi mdi-dots-horizontal\"></i> on UI Screen item that created outside of a module and use \"Set as Start screen\" option. </p> \n                        </span>\n                    </div>\n                    <!-- Publish -->\n                    <span style=\"margin-top: 7px; margin-left: 12px;\">\n                        <div :class=\"{'button-disabled': !hasModel}\">\n                            <button type=\"button\"\n                                :class=\"['publish-button', operationState.publishing.value ? '' : '']\"\n                                @click=\"checkPublishDetails();\"\n                                style=\"border:none; border-radius: 4px;  display: flex;\" id=\"dropdownPublishMenuButton\"\n                                data-bs-toggle=\"dropdown\" aria-expanded=\"false\">\n                                <i class=\"mdi mdi-share-all\" style=\"margin-left: 12px; font-size: 20px;\"></i>\n                                <!-- <div v-else class=\"spinner publishbutonsize\"></div> -->\n                                <span class=\"publish-text\">Publish</span>\n                                <i class=\"mdi mdi-chevron-down\"></i>\n                            </button>\n                            <ul :class=\"['dropdown-menu menu-publish', operationState.publishing.value ? '' : '']\"\n                                :style=\"'box-shadow: 0px 2px 4px #00000029; border: 1px solid #D5D5D5;'\"\n                                aria-labelledby=\"dropdownLivePreviewButton\">\n                                <li @click=\"$event.stopPropagation()\">\n                                    <div\n                                        style=\"margin-top: 24px; display: flex; justify-content: center;\">\n                                        <img src=\"../../../../images/rocket-launch.svg\" />\n                                    </div>\n                                    <!-- <div v-else class=\"spinner\"></div> -->\n                                </li>\n                                <li style=\"display: flex; justify-content: center;\">\n                                    <span class=\"publish-dropdown-text\"\n                                        style=\"margin-top: 20px;\">From here, you can publish your application or see progress details.</span>\n                                    <!-- <span v-else class=\"publish-estimated-time-text\">Estimated time 15 minutes...</span> -->\n                                </li>\n                                <li @click=\"$event.stopPropagation()\">\n                                    <div class=\"device-type\" style=\"border-bottom: 1px solid #EEEEEE; border-top: 1px solid #D5D5D5\">\n                                        <CheckboxComp class=\"module-checkbox\" :check=\"publishToWeb\" @click=\"publishToWeb = !publishToWeb\" />\n                                        <span @click=\"publishToWeb = !publishToWeb\" class=\"deviceTypeName\">Web</span>\n                                        <div class=\"publish-desc\" v-if=\"viewModel.studio.webPublishDate && viewModel.studio.dnsEndpoint\">\n                                            <div style=\"margin-bottom: 4px\">This application is published to web \n                                                <span style=\"font-weight:500\">{{ calculateDate(viewModel.studio.webPublishDate) }}</span> by\n                                                <span style=\"font-weight:500\">{{ viewModel.studio.webPublishedBy }}</span>\n                                             </div>\n                                            <a class=\"pageLink\" :href=\"viewModel.studio.dnsEndpoint\" target=\"_blank\">Go to your web app\n                                                <i class=\"mdi mdi-open-in-new\"></i>\n                                            </a>\n                                        </div>\n                                        <div v-else class=\"publish-desc\">Publish your web application to Studio Playground.</div>\n                                    </div>\n                                    <div class=\"device-type\">\n                                        <CheckboxComp class=\"module-checkbox\" :check=\"publishToMobile\" @click=\"publishToMobile = !publishToMobile\"/>\n                                        <span class=\"deviceTypeName\" @click=\"publishToMobile = !publishToMobile\">Mobile</span>\n                                        <div v-show=\"viewModel.studio.mobilePublishDate && viewModel.studio.dnsEndpoint\">\n                                            <div class=\"publish-desc\">\n                                                <div style=\"margin-bottom: 4px\">Published to StudioApps\n                                                    <span style=\"font-weight:500\">{{ calculateDate(viewModel.studio.mobilePublishDate) }}</span> by \n                                                    <span style=\"font-weight:500\">{{ viewModel.studio.mobilePublishedBy }}</span>\n                                                </div>\n                                                <div class=\"pageLink\"  @click=\"copyToClipboard(studioAppLink, false)\">Copy Link\n                                                <i @click=\"copyToClipboard(studioAppLink, false)\" class=\"mdi mdi-content-copy copy-mobile-link\"></i>\n                                            </div>\n                                            </div>\n        \n                                            <div class=\"mobile-publish-qrcode-div\">\n                                                    <canvas id=\"studioapps-qrcode\" class=\"studioapps-qrcode\" width=\"100\" height=\"100\"/>\n                                            </div>\n                                            <div style=\"display: grid\">\n                                                <span style=\"font-size: 12px;color: #A1A1A1;text-align: center;\">Scan to view your application in <span style=\"font-weight:500\">StudioApps</span></span>\n                                                <div style=\"width: 100%; height: 15px; border-bottom: 1px solid #EEEEEE; text-align: center\">\n                                                    <span style=\"font-size: 12px; background-color: #FFFFFF; padding: 0 10px; color: #A1A1A1;\">or</span>\n                                                </div>\n                                                <div style=\"display: flex; padding: 19px 6px 4px; justify-content: center;\">\n                                                    <div style=\"display: flex; cursor: pointer; align-items: center;\" @click=\"redirectToStore('apple')\">\n                                                        <img src=\"../../../../images/apple-logo.svg\"/>\n                                                        <span class=\"store-text\">Download on <span style=\"font-weight: 500\">AppStore</span></span>\n                                                    </div>\n                                                    <div style=\"display: none; cursor: pointer\" @click=\"redirectToStore('google-play')\">\n                                                        <img src=\"../../../../images/google-play-logo.svg\"/>\n                                                        <span class=\"store-text\">Download on <span style=\"font-weight: 500\">Google Play</span></span>\n                                                    </div>\n                                                </div>\n                                            </div>\n                                        </div>\n                                        <div v-show=\"!viewModel.studio.mobilePublishDate || !viewModel.studio.dnsEndpoint\" class=\"publish-desc\">Publish your mobile application to the Studio Apps in store.</div>\n                                    </div>\n                                </li>\n                                <li style=\"border-bottom: 1px solid #EEEEEE; border-top: 1px solid #EEEEEE; padding: 24px\">\n                    