import { Logger, LogType, LogLevel } from "@stechquick/algae/lib/helpers/logger";
import { DomSocket, DomSocketStatus, ReconnectType } from "@stechquick/algae/lib/helpers/socket/domSocket";
import { ICreateFolderMsg, IDeleteFilesMsg, IDeleteFolderMsg, IFileContent, IGetFilesMsg, IListFilesMsg, INetworkMessageRequest, INetworkMessageResponse, ISaveFilesMsg, RequestOptions, ResponseOptions } from "../IFileNetworkMessage";
import { CreatePromiseData, IPromiseData } from "@stechquick/algae/lib/helpers/createPromiseData";
import { FileSubSystemStatusChangeCb, IFileSubSystem } from "./IFileSubSystem";

interface ITrial {
    socket: DomSocket;
    verified?: boolean;
    timer?: number;
}


export class NetworkSubSystem implements IFileSubSystem {
    socket?: DomSocket;
    msgCounter: number;
    awaitMsgList: Record<string, { sendTime: number, prom: IPromiseData<ResponseOptions> } | undefined>;
    statusChangesCb?: FileSubSystemStatusChangeCb;
    lastSocketStatus?: DomSocketStatus;

    constructor() {
        this.msgCounter = 0;
        this.awaitMsgList = {};
    }

    public WatchStatusChanges(cb: FileSubSystemStatusChangeCb) {
        this.statusChangesCb = cb;
    }

    public Disconnect() {
        this.socket?.Close(4201, "userClosed");
    }

    public Discover() {
        this.Disconnect();
        const prom = CreatePromiseData<boolean>();
        const networkTrials: Array<ITrial> = [];
        const startPort = 3000;
        const portScanCount = 10;
        for (let i = 0; i < portScanCount; i++) {
            let curTrial: ITrial;
            const url = `ws://127.0.0.1:${startPort + i}`;
            const curSocket = new DomSocket(url, {
                logCallBack: log => {
                    if (!curTrial.verified) { return; }
                    let logType: LogType;
                    switch (log.level) {
                        default:
                        case LogLevel.debug: logType = LogType.Debug; break;
                        case LogLevel.error: logType = LogType.Error; break;
                        case LogLevel.log: logType = LogType.Trace; break;
                        case LogLevel.trace: logType = LogType.Trace; break;
                        case LogLevel.warn: logType = LogType.Warning; break;
                    }
                }
            }).Connect();
            const startTimer = (timems: number, closeReason: string) => curTrial.timer = window.setTimeout(() => {
                Logger.Log({ logMessage: "failed: " + url, logType: LogType.Debug });
                curTrial.verified = false;
                curSocket.Close(4200, closeReason);
                if (networkTrials.filter(trial => trial.verified === false).length == portScanCount) {
                    prom.resolver(false);
                }
            }, timems);
            const stopTimer = () => window.clearTimeout(curTrial.timer);
            const onMessage = (msg: string) => {
                if (msg == "hello nemo") {
                    stopTimer();
                    curTrial.verified = true;
                    curSocket.OnMessage = this.onMessage.bind(this);
                    curSocket.onSocketStatus = this.onSocketStatus.bind(this);
                    this.lastSocketStatus = DomSocketStatus.Connected;
                    curSocket.ReConnect = { type: ReconnectType.BackOff, intervalMs: 1000 };
                    this.socket = curSocket;
                    prom.resolver(true);
                    this.statusChangesCb?.({ status: "connected", text: "FileSystem Connected 😍" });
                }
            };

            curSocket.OnConnect = () => {
                stopTimer();
                curSocket.OnMessage = onMessage;
                curSocket.Send("hello cli");
                startTimer(5000, "missed hello timer");
            };
            curTrial = networkTrials[i] = { socket: curSocket };
            curSocket.Connect();
            startTimer(10000, "missed connect timer");
        }
        return prom.promise;
    }
    private onSocketStatus(status: DomSocketStatus) {
        if (this.lastSocketStatus == DomSocketStatus.Connected && status != DomSocketStatus.Connected) {
            this.socket!.onSocketStatus = () => { };
            this.statusChangesCb?.({ status: "disconnected", text: "Lost filesystem 😟" });
        }
        this.lastSocketStatus = status;
    }

    public async ListFiles(path: string) {
        const msg: IListFilesMsg = { type: "list_files", path: path };
        const resp = await this.send_internal(msg, true);
        if (!resp) { throw "not connected"; }
        if (resp.type != "list_files_resp") { throw "unexpected response"; }
        return resp;
    }


    public async SaveFiles(files: Array<IFileContent>) {
        const msg: ISaveFilesMsg = {  type: "save_files", files };
        const resp = await this.send_internal(msg, true);
        if (!resp) { throw "not connected" };
        if (resp.type != "save_files_resp") { throw "unexpected response"; }
        return { result: resp.result, filePaths: resp.filePaths };
    }
    public async CreateFolder(folderName: string) {
        const msg: ICreateFolderMsg = {  type: "create_folder", folderName };
        const resp = await this.send_internal(msg, true);
        if (!resp) { throw "not connected" };
        if (resp.type != "create_folder_resp") { throw "unexpected response"; }
        
    }
    public async DeleteFolder(folderName: string) {
        const msg: IDeleteFolderMsg = {  type: "delete_folder", folderName };
        const resp = await this.send_internal(msg, true);
        if (!resp) { throw "not connected" };
        if (resp.type != "delete_folder_resp") { throw "unexpected response"; }
        
    }
    public async DeleteFiles(files: Array<string>) {
        const msg: IDeleteFilesMsg = { type: "delete_files", files };
        const resp = await this.send_internal(msg, true);
        if (!resp) { throw "not connected" };
        if (resp.type != "delete_files_resp") { throw "unexpected response"; }
        return resp.result;
    }
    public async GetFiles(files: string[]) {
        const msg: IGetFilesMsg = { type: "get_files", files };
        const resp = await this.send_internal(msg, true);
        if (!resp) { throw "not connected" };
        if (resp.type != "get_files_resp") { throw "unexpected response"; }
        return resp.files;
    }



    private send_internal(msg: RequestOptions, returnPromise: boolean) {
        if (!this.socket) { return false; }
        const data: INetworkMessageRequest = { msg, id: `${this.msgCounter++}` };
        this.socket.Send(data);
        let prom: IPromiseData<ResponseOptions> | undefined;
        if (returnPromise) {
            prom = CreatePromiseData();
            this.awaitMsgList[data.id] = { sendTime: Date.now(), prom };
        }
        return prom?.promise || undefined;
    }


    private onMessage(mes: any) {
        const msg = <INetworkMessageResponse>JSON.parse(mes);
        if (msg.reply) {
            this.awaitMsgList[msg.reply]?.prom.resolver(msg.msg);
            return;
        }

    }

}