import { Hook } from "@stechquick/algae/lib/helpers/hook";
import { Logger, LogType } from "@stechquick/algae/lib/helpers/logger";
import { IFileContent, IFileSystemItems } from "../IFileNetworkMessage";
import { FileSubSystemStatus, IFileSubSystem, ISaveFilesResult } from "./IFileSubSystem";
import { NetworkSubSystem } from "./networkSubSystem";

export class FileSystem {
    private subSystem?: IFileSubSystem;
    private status: FileSubSystemStatus = "disconnected";
    public FileSystemStatusHook = new Hook<(status: FileSubSystemStatus) => void>("FileSystemStatusHook");

    constructor() {
    }

    public set SubSystem(subSystem: IFileSubSystem) {
        this.subSystem = subSystem;
        this.subSystem.WatchStatusChanges?.((newStatus) => {

            this.status = newStatus.status;
            this.FileSystemStatusHook.triggerer(newStatus.status);
        });
    }

    public async Connect(subSystem?: IFileSubSystem): Promise<boolean> {
        if (subSystem) { this.SubSystem = subSystem; }
        if (!this.subSystem) { return false; }
        const retVal = await this.subSystem.Discover();
        if (retVal) {
            Logger.Log({ logMessage: "File System connected. Lets go!!! 🤩", logType: LogType.Warning });
        } else {
            Logger.Log({ logMessage: "No FileSystem, all alone 😟", logType: LogType.Debug });
        }

        return retVal;
    }
    public async Disconnect() {
        if (!this.subSystem) { return false; }
        this.subSystem.Disconnect();
    }

    public get Status(): FileSubSystemStatus {
        return this.status;
    }

    public async ListFiles(path: string): Promise<IFileSystemItems | undefined> {
        if (!this.subSystem) { return; }
        return await this.subSystem.ListFiles(path);
    }

    public async SaveFiles(files: Array<IFileContent>): Promise<ISaveFilesResult | undefined> {
        if (!this.subSystem) { return; }
        return await this.subSystem.SaveFiles(files);
    }

    public async CreateFolder(folderName : string): Promise<void> {
        if (!this.subSystem) { return; }
        return await this.subSystem.CreateFolder(folderName);
    }

    public async DeleteFolder(folderName : string):  Promise<void>  {
        if (!this.subSystem) { return; }
        return await this.subSystem.DeleteFolder(folderName);
    }

    public async DeleteFiles(files: Array<string>): Promise<boolean | undefined> {
        if (!this.subSystem) { return; }
        return await this.subSystem.DeleteFiles(files);
    }

    public async GetFiles(files: Array<string>): Promise<Array<IFileContent> | undefined> {
        if (!this.subSystem) { return; }
        return await this.subSystem.GetFiles(files);
    }
}