export interface IQRCodeOptions {
    colorDark?: string;
    colorLight?: string;
    errorCorrectionLevel?: "L" | "M" | "Q" | "H";
    margin?: number;
    centeredLogoURL?: string;
    centeredLogoWidth?: number;
    centeredLogoHeight?: number;
}

export interface IQRCode {
    drawToCanvas(canvas: HTMLElement, text: string, options?: IQRCodeOptions): Promise<void>;
}

export const IQRCode = Symbol.for("IQRCode");
