import { IObject } from "../model/IObject";
import { IApplication, IExportJobArtifactInfoItem, IExportJobItem, IExportJobStepItem, IFolder, IModel, IModule, ILoggedInUser } from "../model/models";

export interface IViewModelManager {
    isSystemUser: boolean;
    findObject(objectId: string): IExportJobItem | IExportJobStepItem | IExportJobArtifactInfoItem | IApplication | IModule | IFolder | IModel | undefined;
    findModel(modelId: string): IModel | undefined;
    traverseChildren(items: Array<(IModule | IFolder | IModel)>, cb: (item: IModule | IFolder | IModel, index: number, containerArray: Array<IModule | IFolder | IModel>) => boolean): boolean;
    removeModel(model: IModel): void;
    removeModule(module: IModule): void;
    removeApplication(app: IApplication): void;
    removeExportJob(jobID: string): void;
    removeObject(object: IObject): void;
    modelCreated(model: IModel): void;
    moduleCreated(module: IModule): void;
    folderCreated(folder: IFolder): void;
    applicationCreated(app: IApplication): IApplication;
    exportJobCreated(job: IExportJobItem): void;
    objectUpdated<TObject extends IApplication | IModule | IModel | IFolder>(object: TObject): TObject;
    findApplication(name: string): IApplication | undefined;
    reset(): void;
    setApplicationList(apps: Array<IApplication>): void;
    setApplicationDetails(appID: string, details: IApplication["details"]): void;
    setExportJobItemList(jobs: Array<IExportJobItem>): void;
    setExportJobSteps(jobID: string, steps: Array<IExportJobStepItem>): void;
    setExportJobStepLogs(stepID: string, logs: Array<string>): void;
    setExportJobArtifacts(jobID: string, artifacts: Array<IExportJobArtifactInfoItem>): void;
    flatMapItems<T>(items: Array<(IModule | IFolder | IModel)>, cb: (item: IModule | IFolder | IModel, path: string) => T | undefined): Array<T>;
}

export const IViewModelManager = Symbol.for("IViewModelManager");