        { closable: true, title: "Custom Preview Settings", width: "800px", height: "705px" }
    );
    document.getElementById("dropdownLivePreviewButton")!.setAttribute("aria-expanded", "false")
}

function hoverLogo() {
    hoverValue.value = true;
}
function unHover() {
    hoverValue.value = false;
}

function setExportType(value: string){
    exportAppType.value = value;
}

async function checkPreviews() {
    showLoading.value = true;
    const ariaExpanded = document.getElementById("dropdownLivePreviewButton")!.getAttribute("aria-expanded");
    if (ariaExpanded == "false") return;
    let livePreviewList = await livePreviwUseCase.listLivePreviewInfo(viewModel.studio.appId);
    showLoading.value = false;

    if (livePreviewList && livePreviewList.length > 0) {
        const customPreview = livePreviewList.find(lp => lp.type == "custom") as ICustomLivePreview;
        const selectedPreview = livePreviewList.find(lp => lp.selected);
        previewType.value = selectedPreview!.type;
        if (!showQRCode.value) {
            showQRCode.value = true;
            const canvas = document.getElementById("qrcode");
            await livePreviwUseCase.createLivePreviewQRCode(canvas!, selectedPreview!.appID);
        }
        if (customPreview) {
            const date = new Date(customPreview.creationInfo.date)
            customPreviewLastUpdated.value = date.getDate() + "." + (date.getMonth() + 1) + "." + date.getFullYear() + " " + (date.getHours() < 10 ? "0" : "") + date.getHours() + ":" + (date.getMinutes() < 10 ? "0" : "") + date.getMinutes();
            showCustomPreview.value = true;
        }
    } else {
        showCustomPreview.value = false;
        previewType.value = "default";
    }
}

async function openLivePreviewTab() {
    const appID = viewModel.studio.appId;
    const appName = viewModel.studio.appName;

    await executor.execute(async () => {
        let lpItem = await livePreviwUseCase.getLivePreviewInfo(appID, { selected: true });
        if (!lpItem) {
            await livePreviwUseCase.createLivePreview({ appID, type: "default", selected: true });
        }
        window.open(`../../livepreview/${appID}/${appName}`, "_blank")
    }, { loading: true });
}

function openFeedbackDialog () {
  dialog.showDialog(
        defineAsyncComponent(() => import(`../../components/dialogs/giveUsFeedbackDialog.vue`)),
        { closable: true, title: "Give Us Feedback", width: "540px", maxHeight: "706px" },
    );
}
async function triggerAutoSave() {
    fileSystem.SubSystem =new NetworkSubSystem();
    await fileSystem.Connect();
    const applicationID = routerParams.appID;
    exportAppUseCase.syncFiles(applicationID, fileSystem);


}

function handleStudioOrCustom(selectedType: string) {
    if (showCustomPreview.value) {
        executor.execute(
            async () => {
                await livePreviwUseCase.updateSelectedLivePreview(viewModel.studio.appId);
                previewType.value = selectedType;
            },
            { loading: true }
        )
    }
}

function publishApplication() {
    operationState.publishing.value = !operationState.publishing.value;
    const platformSelection: IPlatformSelection = { web: publishToWeb.value, mobile: publishToMobile.value };
    const title = platformSelection.web ? platformSelection.mobile ? "Web and Mobile" : "Web" : platformSelection.mobile ? "Mobile" : ""; 
    dialog.showDialog(
        defineAsyncComponent(() => import(`../dialogs/publish/publishDialog.vue`)),
        { closable: true, title: "Publish Modules to " + title, width: "800px", height: platformSelection.web ? "622px" : "555px" },
        { screenType: "Deploy", platformSelection }
    );
}

function exportAppClickEvent(){
    if(exportAppType.value == "create")
        downloadApplication();
    else if(exportAppType.value == "sync")
        triggerAutoSave();
}

function copyExportCommand(){
    navigator.clipboard.writeText("symphony-cli studio-sync");
}

function downloadApplication() {
    operationState.downloading.value = !operationState.downloading.value;

    dialog.showDialog(
        defineAsyncComponent(() => import(`../dialogs/publish/publishDialog.vue`)),
        { closable: true, title: "Chose Modules to Download", width: "800px", height: "531px" },
        { screenType: "Export" }
    );
}

function seeProgressDetails() {
    page.openPage(
        defineAsyncComponent(() => import('../dialogs/publish/publishHistory.vue')),
        {name : "Progress Details"}
    );
}
function checkPublishDetails() {
    viewModel.studio.dnsEndpoint = currentApp.value?.cloud?.azure?.dnsEndpoint;
    viewModel.studio.webPublishDate = currentApp.value?.cloud?.azure?.web?.publishDate.toLocaleString();
    viewModel.studio.webPublishedBy = currentApp.value?.cloud?.azure?.web?.publishedBy.split("@")[0];
    viewModel.studio.mobilePublishDate = currentApp.value?.cloud?.azure?.mobile?.publishDate.toLocaleString();
    viewModel.studio.mobilePublishedBy = currentApp.value?.cloud?.azure?.mobile?.publishedBy.split("@")[0];
    viewModel.studio.publishId = currentApp.value?.publishmentId;
    mobilePublishedQRCode();
}
function mobilePublishedQRCode() {
    const QRCode = container.resolve<IQRCode>(IQRCode);
    const canvas = document.getElementById("studioapps-qrcode") as HTMLElement;
    studioAppLink.value = "https://studio.onplateau.com/studioapps/" + viewModel.studio.publishId;
    QRCode.drawToCanvas(canvas, studioAppLink.value);

}
function calculateDate(createDate: string) {
    const date = new Date(createDate);
    if(date) {
        const minutes = parseInt(String(((new Date()).getTime() - date.getTime()) / (60 * 1000)), 10)
        const hours = parseInt(String(minutes / 60), 10);
        const days = parseInt(String(hours / 24), 10);
        const weeks = parseInt(String(days / 7), 10);
        const months = parseInt(String(weeks / 4), 10);
        const years = parseInt(String(months / 12), 10);
        if(years > 0) {
            return years.toFixed() + ` year${years < 2 ? '' : 's'} ago`;
        } else if(months > 0) {
            return months.toFixed() + ` month${months < 2 ? '' : 's'} ago`;
        } else if(weeks > 0) {
            return weeks.toFixed() + ` week${weeks < 2 ? '' : 's'} ago`;
        } else if(days > 0) {
            return days.toFixed() + ` day${days < 2 ? '' : 's'} ago`;
        } else if(hours > 0) {
            return hours.toFixed() + ` hour${hours < 2 ? '' : 's'} ago`;
        } else if(minutes > 0) {
            return minutes.toFixed() + ` minute${minutes < 2 ? '' : 's'} ago`;
        }
    }
    return "few seconds ago";
}
function copyToClipboard(element: string, isFromInnerHtml: boolean) {
    if(isFromInnerHtml) {
        let text = document.getElementById(element)?.innerHTML;
        navigator.clipboard.writeText(text!);
    }
    else {
        navigator.clipboard.writeText(element);
    }

}

function redirectToStore(store: string) {
    switch(store) {
        case "apple": window.open("https://apps.apple.com/tr/app/studioapps/id6446583055", "_blank"); break;
        // case "google-play": window.open("", "_blank"); break;
    }
}
